import { gql } from 'apollo-boost';

export const SEARCH = gql`
  query($name: String!){
    search(query: $name, type: REPOSITORY, first: 10) {

      repositoryCount
      nodes {
          ... on Repository {
            id
            name
            stargazers {
              totalCount
            }
            owner {
              login
              avatarUrl
            }
            primaryLanguage {
              id
              name
            }
            updatedAt
          }
      }
    }
  }
`;
